import React from "react"
import { ChatDataProvider } from "@rp/react/web/chat"
import { SelectedFile } from "../../../../../components/Hub/Files/files.types"
import { useDisabledFeatures } from "../../../../../hooks/useDisabledFeatures"
import { useChatPaneContainer } from "../../hooks/useChatPaneContainer"
import { ChatPane } from "./ChatPane"

type ChatPaneContainerProps = {
  close: () => void
  width: string | null
  selectedFiles: Array<SelectedFile>
}

function ChatPaneContainer({ close, width, selectedFiles }: ChatPaneContainerProps) {
  const { chatId, onChatCreated } = useChatPaneContainer()
  const { sourceFilters } = useDisabledFeatures()

  return (
    <ChatDataProvider
      id={chatId}
      origin="files"
      onChatCreated={onChatCreated}
      enabledSources={sourceFilters}
    >
      <ChatPane width={width} close={close} selectedFiles={selectedFiles} />
    </ChatDataProvider>
  )
}

export { ChatPaneContainer }
